/* main navigation file */

.menu-all-pages-container {
    background-color: $black;
    color: $white;
    position: sticky;
    top: 0;

    ul {
        display: flex;

        li {
            transition: all 0.3s;
            position: relative;

            a {
                color: $white;
                display: block;
                text-decoration: none;
                padding: 1rem 1.5rem;
                font-size: 1rem;
            }
            
            &:hover, 
            &.current-menu-item {
                background-color: $red;
            }

            &:hover {
                > ul {
                    display: block;
                    position: absolute;
                    background-color: $red;
                    top: 3rem;
                    left: 0;
                    z-index: 1;
                    min-width: 10rem;

                    li {
                        &:hover {
                            background-color: $black;
                            padding-left: 0.2rem;
                        }
                    }
                }
            }
        }

        ul {
            display: none;
        }
    }
}