html, body {
    background-color: $white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.1rem;
}

main {
    width: 80%;
    max-width: 80rem;
    margin: 2rem auto;
}

.container {
    width: 80%;
    max-width: 80rem;
    margin: initial auto;
}


h1, h2, h3, h4, h5, h6 {
    font-family: Dosis;
    margin-bottom: 1.5rem;
    font-weight: bold;
    color: $grey;
}

h1 {
    font-size: 5rem;
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 3rem;
}

h4 {
    font-size: 2rem;
}

p {
    line-height: 1.25rem;
    margin-bottom: 1rem;
}