/* Footer styles */

.wp-footer {
    background-color: $ltgray;
    padding: 3rem 0 4rem 0;

    .container {
        max-width: 80rem;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
    }

    .footer-nav {
        display: flex;

        li {
            margin-right: 1rem;

            a {
                color: $grey;
                text-decoration: none;
                transition: all 0.2s;
                font-size: 0.9rem;

                &:hover {
                    color: $black;
                }
            }
        }
    }

    .copyright {
        font-size: 0.9rem;
        color: $black;
    }
}